h1,
h2,
h3,
h4,
h5,
h6,
ul,
li,
p,
blockquote {
  margin: 0;
  padding: 0;
}

input,
textarea,
button {
  border: 0;
  border-radius: 0;
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

input:hover, input:active, input:focus,
textarea:hover,
textarea:active,
textarea:focus,
button:hover,
button:active,
button:focus {
  outline: none;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

embed,
iframe,
img,
object,
video {
  max-width: 100%;
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto Slab';
  background-color: #aaa;
}

.navbar, .megamenu {
  position: relative;
}

.navbar {
  width: 80%;
  margin: auto;
}

.megamenu li {
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  position: relative;
  min-width: 12rem;
  padding: .5rem;
}

.megamenu {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  align-items: stretch;
}

.megamenu > li {
  min-width: auto;
  display: flex;
  flex: 1 0 0%;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  transition: color 0.5s ease-out, background-color 0.5s ease-out;
  background-color: #334;
  overflow: hidden;
}

.megamenu > li[class~=has-submenu] {
  overflow: visible;
  background-color: fuchsia;
}

.megamenu > li.burger {
  display: none;
  font-weight: 900;
  flex: 0 1 auto;
  justify-content: flex-end;
  color: #a7a7a7;
}

.megamenu > li.social-icons {
  display: flex;
  flex-wrap: nowrap;
  justify-content: flex-end;
  align-items: center;
  flex: 0;
  min-width: 12rem;
}

.megamenu > li.social-icons a {
  padding-right: .5rem;
  text-decoration: none;
  flex: 1 0 0%;
}

.megamenu > li > .ellipsis {
  flex: 1 1 0%;
  justify-content: center;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.megamenu > li > .ellipsis > svg {
  flex-shrink: 0;
}

.megamenu > li > .ellipsis > span {
  display: inline-block;
  margin-left: .5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.megamenu > li:hover, .megamenu > li .icon:hover {
  background-color: #494961;
}

.site-header--is-stack.megamenu > li:nth-child(n+4) {
  display: flex;
  min-width: 100%;
  background-color: #9494af;
}

.site-header--is-stack.megamenu > li:nth-child(n+4):hover {
  background-color: #494961;
}

@media only screen and (max-width: 480px) {
  .megamenu > li:nth-child(n+4) {
    display: none;
  }
  .megamenu > li.burger {
    display: flex;
  }
  .megamenu > li.social-icons {
    justify-content: flex-start;
  }
}

.megamenu li > a {
  display: flex;
  flex: 1;
}

.megamenu li > a:hover {
  color: #dadada;
}

.megamenu li.has-submenu > a {
  justify-content: center;
  flex: 1;
}

.megamenu .submenu {
  display: none;
  flex-wrap: wrap;
  justify-content: flex-start;
  flex: 1;
  position: absolute;
  top: 100%;
  z-index: 1;
  background-color: #3e3e53;
}

.megamenu .submenu ul {
  display: flex;
  flex-wrap: wrap;
  flex: 1 0 0%;
}

.megamenu li > ul[class~=submenu] {
  display: none;
}

.megamenu li:hover > ul[class~=submenu] {
  display: flex;
}

a, .has-link-appearance {
  transition: color 0.5s ease-out, background-color 0.5s ease-out;
  text-decoration: none;
  color: #a7a7a7;
}

a:hover, .has-link-appearance:hover {
  color: #dadada;
}

.icon {
  width: 1rem;
  height: 1rem;
  fill: #a7a7a7;
}

.icon:hover {
  fill: #dadada;
}

li.grow-no {
  flex: 0 1 auto;
}
